import {eventTypeBuilder, configBuilder} from "./builder"


export const BROWSER_EVENT = eventTypeBuilder()
    .whitType('event')
    .withId('browser')
    .withName('Navegador')
    .withLabel('Navegador')
    .withSrc('https://upload.wikimedia.org/wikipedia/commons/thumb/e/e0/Browser_ballonicon2.svg/1024px-Browser_ballonicon2.svg.png')
    .addConfig(configBuilder()
        .select('action')
        .withLabel('Ação')
        .addOption('navigate', 'Acessar')
        .addOption('emulate', 'Emular Smartphone')
        .addOption('resize', 'Redimensionar')
        .addOption('setSession', 'Definir Sessão')
        .addOption('screenshot', 'Capturar Tela')
        .build()
    )
    .addConfig(configBuilder()
        .select('headless')
        .withLabel('Modo Headless')
        .addOption('true', 'Sim')
        .addOption('false', 'Não')
        .build(),
    )
    .addConfig(configBuilder()
        .text('url')
        .withLabel('URL')
        .withShow((config) => {
            return config.action === 'navigate'
        })
        .build()
    )
    .addConfig(configBuilder()
        .number('timeout')
        .withLabel('Tempo de Espera em milisegundos')
        .withShow((config) => {
            return config.action === 'navigate'
        })
        .build()
    )
    .addConfig(configBuilder()
        .select('device')
        .withLabel('Dispositivo')
        .addOption('pixel2', 'Pixel 2')
        .addOption('iphoneX', 'iPhone X')
        .withShow((config) => {
            return config.action === 'emulate'
        })
        .build()
    )
    .addConfig(configBuilder()
        .number('width')
        .withLabel('Largura')
        .withShow((config) => {
            return config.action === 'resize'
        })
        .build()
    )
    .addConfig(configBuilder()
        .number('height')
        .withLabel('Altura')
        .withShow((config) => {
            return config.action === 'resize'
        })
        .build()
    )
    .addConfig(configBuilder()
        .checkbox('fullPage')
        .withLabel('Capturar Tela Inteira')
        .withShow((config) => {
            return config.action === 'screenshot'
        })
        .build()
    )
    .addConfig(configBuilder()
        .text('path')
        .withLabel('Caminho')
        .withShow((config) => {
            return config.action === 'screenshot'
        })
        .build()
    )
    .build()
    