import { eventTypeBuilder, configBuilder } from "./builder";

export const MOUSE_EVENT = eventTypeBuilder()
    .whitType('event')
    .withName("mouse")
    .withLabel("Mouse")
    .withSrc("https://www.svgrepo.com/show/57270/computer-mouse.svg")
    .addConfig(
        configBuilder()
            .select("action")
            .withLabel("Ação")
            .addOption("click", "Clicar")
            .addOption("doubleClick", "Duplo Clique")
            .addOption("rightClick", "Clique Direito")
            .addOption("move", "Mover")
            .addOption("hold", "Segurar")
            .addOption("release", "Soltar")
            .build()
    )
    .addConfig(
        configBuilder()
            .select("selectorType")
            .withLabel("Tipo de Seletor")
            .addOption("css", "Seletor CSS")
            .addOption("xpath", "XPath")
            .addOption("text", "Texto")
            .addOption("position", "Posição")
            .withShow((config) => {
                return config["action"] === "click" || config["action"] === "doubleClick" || config["action"] === "rightClick" || config["action"] === "hover" || config["action"] === "move";
            })
            .build()
    )
    .addConfig(
        configBuilder()
            .number("x")
            .withLabel("X")
            .withConfigValue(0)
            .withShow((config) => {
                return config["selectorType"] === "position" && config["action"] !== "hold" && config["action"] !== "release";
            })
            .build()
    )
    .addConfig(
        configBuilder()
            .number("y")
            .withLabel("Y")
            .withShow((config) => {
                return config["selectorType"] === "position" && config["action"] !== "hold" && config["action"] !== "release";
            })
            .withConfigValue(0)
            .build()
    )
    .addConfig(
        configBuilder()
            .text("selector")
            .withLabel("Valor do Seletor")
            .withShow((config) => {
                return config["selectorType"] !== "position"  && config["action"] !== "hold" && config["action"] !== "release";;
            })
            .build()
    ).addConfig(
        configBuilder()
            .select("button")
            .withLabel("Botão")
            .addOption("left", "Esquerdo")
            .addOption("middle", "Meio")
            .addOption("right", "Direito")
            .withShow((config) => {
                return config["action"] === "hold" || config["action"] === "release";
            })
            .build()
    )
    .build();
            