export const eventTypeBuilder = () =>  ({
    type: 'event',
    id : '',
    name : '',
    label : '',
    src : '',
    config : [],
    whitType( type ) {
        this.type = type
        return this
    },
    withId( id ) {
        this.id = id
        return this
    },
    withName( name ) {
        this.name = name
        return this
    },
    withLabel( label ) {
        this.label = label
        return this
    },
    withSrc( src ) {
        this.src = src
        return this
    },
    withConfig( config ) {
        this.config = [...this.config, ...config ]
        return this
    },
    addConfig( config ) {
        if (!this._validateConfig( config ) ) throw new Error( 'Invalid config', config )
        
        this.config.push( config )

        return this
    },

    _validateConfig( config ) {
        const type = config.type
        

        if ( type === 'select' ) {
            return config.name && config.type && config.label && config.options
        }
        
        
        return config.name && config.type && config.label
        

    },
    withGherkinCb( gherkin ) {
        this.gherkin = gherkin
        return this
    },
    build() {
        const commonAttributes = [ 'type', 'id', 'name', 'label', 'src', 'config', 'gherkin' ]
        const result = {}

        for ( let attr of commonAttributes ) {
            result[attr] = this[attr]
        }

        return result
    },


})



export const configBuilder = () => ({
    name : '',
    type : '',
    label : '',
    options : [],
    configValue : '',
    select(name) {
        this.type = 'select'
        this.name = name
        return this
    },
    file (name) {
        this.type = 'file'
        this.name = name
        return this
    },
    text(name) {
        this.type = 'text'
        this.name = name
        return this
    },
    number(name) {
        this.type = 'number'
        this.name = name
        return this
    },
    checkbox(name) {
        this.type = 'checkbox'
        this.name = name
        return this
    },
    radio(name) {
        this.type = 'radio'
        this.name = name
        return this
    },

    withName( name ) {
        this.name = name
        return this
    },
    withLabel( label ) {
        this.label = label
        return this
    },
    withOptions( options ) {
        this.options = options
        return this
    },
    addOption( value, label ) {
        const option = { value, label }
        if (!this.__validateOption( option )) {
            throw new Error( 'Invalid option', option )
        }

        if (this.options.length === 0) {
            this.configValue = value
        }

        this.options.push( option )
        return this
    },
    __validateOption ( option ) {
        return option.value && option.label
    },
    withShow( show ) {
        this.show = show
        return this
    },
    withShowOption( showOption ) {
        this.showOption = showOption
        return this
    },
    withConfigValue( configValue ) {
        this.configValue = configValue
        return this
    },
    build() {
        const commonAttributes = [ 'name', 'type', 'label', 'configValue', 'show', 'showOption' ]
        const selectAttributes = [ 'options' ]
        let attributes = commonAttributes
        const result = {}

        if ( this.type === 'select' ) {
            attributes = [...commonAttributes, ...selectAttributes]
        }
        
        for ( let attr of attributes ) {
            if ( !this[attr] ) continue
            result[attr] = this[attr]
        }

        return result
    },
   
})