import { eventTypeBuilder, configBuilder } from "./builder";
const actionConfig = configBuilder()
    .select('action')
    .withLabel('Ação')
    .addOption('assert', 'Validar')
    .addOption('wait', 'Aguardar')
    .withConfigValue('assert')
    .build();
const actionTypeConfig =  configBuilder()
    .select('actionType')
    .withLabel('Tipo de Ação')
    .addOption('content', 'Conteúdo')
    .addOption('request', 'Requisição')
    .addOption('response', 'Resposta')
    .addOption('browser', 'URL do Navegador')
    .withConfigValue('content')
    .build();


// Request and Response Configs
const requestResponseConfig = [
    configBuilder()
        .select('method')
        .withLabel('Método')
        .addOption('get', 'GET')
        .addOption('post', 'POST')
        .addOption('put', 'PUT')
        .addOption('delete', 'DELETE')
        .withConfigValue('get')
        .withShow((config) => {
            return config['actionType'] === 'request' || config['actionType'] === 'response'
        })
        .build(),
    configBuilder()
        .text('url')
        .withLabel('URL')
        .withShow((config) => {
            return config.actionType === 'browser' || config['actionType'] === 'request' || config['actionType'] === 'response'
        })
        .build(),
    configBuilder()
        .number('statuscode')
        .withLabel('Status Code')
        .withShow((config) => {
            return config['actionType'] === 'response'
        }) 
        .build(),
]

const selectorTypeConfig = configBuilder()
    .select('selectorType')
    .withLabel('Tipo de Seletor')
    .addOption('css', 'CSS')
    .addOption('xpath', 'XPath')
    .addOption('text', 'Texto')
    .withConfigValue('css')
const selectorConfig = configBuilder()
    .text('selector')
    .withLabel(({actionType}) => {
        return actionType === 'text' ? 'Texto' : 'Seletor'
    })
const showAttributeConfig = configBuilder()
    .checkbox('showAttribute')
    .withLabel('Buscar Valor do Atributo do Elemento')
const attributeValueConfig = configBuilder()
    .text('attributeValue')
    .withLabel('Atributo do Elemento')
    .withShow((config) => {
        return config.showAttribute
    })


// Content Configs
const contentConfig = [
    selectorTypeConfig
        .withShow(({actionType}) => {
            return actionType === 'content'
        })
        .build(),
    selectorConfig
        .withShow(({actionType}) => {
            return actionType === 'content'
        })
        .build(),
    showAttributeConfig
        .withShow(({actionType, action}) => {  
            return action !== 'wait' && actionType === 'content'
        })
        .build(),
    attributeValueConfig
        .withShow(({action, actionType, showAttribute}) => {
            
            return action !== 'wait' && showAttribute && actionType === 'content'
        })
        .build(),
]
const assertConfig = [
    configBuilder()
        .select('assertType')
        .withLabel('Tipo de Validação')
        .addOption('equals', 'Igual')
        .addOption('contains', 'Contém')
        .addOption('not-contains', 'Não Contém')
        .addOption('exists', 'Existe')
        .addOption('not-exists', 'Não Existe')
        .addOption('visible', 'Visível')
        .addOption('not-visible', 'Não Visível')
        .withConfigValue('equals')
        .withShow((config) => {
            return config['actionType'] === 'content'
        })
        .withShowOption(({value}, {showAttribute, action}) => {

            if (action === 'wait') {
                return value !== 'contains' && value !== 'not-contains' && value !== 'equals'
            }
            if (showAttribute) {
                return value !== 'visible' && value !== 'not-visible'
            }

            return true
        })
        .build(),
    configBuilder()
        .text('value')
        .withLabel('Valor')
        .withShow((config) => {
            return config['actionType'] === 'content' && config['assertType'] !== 'exists' && config['assertType'] !== 'not-exists' && config['assertType'] !== 'visible' && config['assertType'] !== 'not-visible'
        })
        .build(),
]

const commonFields = [
    ...requestResponseConfig,
    ...contentConfig,
    ...assertConfig,
    configBuilder()
        .number('timeout')
        .withLabel('Tempo de Espera em milisegundos')
        .withConfigValue(15000)
        .build(),


]
  
export const VERIFICATION_EVENT = eventTypeBuilder()
    .whitType('event')
    .withId('verification')
    .withName('Verificação')
    .withLabel('Verificação')
    .withSrc('https://cdn-icons-png.flaticon.com/512/7398/7398228.png')
    .addConfig(actionConfig)
    .addConfig(actionTypeConfig)
    .withConfig(commonFields)
    .build()