/* eslint-disable react/prop-types */
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import styled from 'styled-components'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import TextField from '@material-ui/core/TextField'
import React from 'react'
import { useBoard } from '../../context/BoardProvider'
import { ADD_NODE, SET_NODE_CONFIG, SET_NODE_LABEL } from '../../constants/actions'
import { eventTypes } from '../../utils'

const PaperStyled = styled(Paper)`
  height: 100%;
  width: 100%;
  box-shadow: none;
  background-image: url(${(props) => (props.src ? props.src : 'none')});
  background-repeat: no-repeat;
  background-size: ${(props) => (props.$isEvent ? 'contain' : 'cover')};
  background-position: ${(props) => (props.$isEvent ? 'center' : 'top')};
  position: relative;
  margin-bottom: 10px;
  cursor: pointer;

  ${(props) =>
    props.$isEvent &&
    `
    background-color: #f1f1f1;
    border-radius: 0;
  `};
`

const GridItemStyled = styled(Grid)`
  width: 140px;
  height: 140px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;

  :hover {
    border-radius: 2px;
    box-shadow: 0 0 0 2px #4c6fff;
    transform: rotate(2deg);
  }

  ${(props) =>
    props.$isEvent &&
    `
    border-radius: 4px;
    border: solid 2px #e5e8f5;
    &&& {
      padding: 0;
    }
  `};
`

const Title = styled.div`
  color: #50525e;
  text-align: center;

  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  font-size: 12px;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  padding: 0 10px;
  white-space: nowrap;
  background-color: #e5e8f5;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  font-weight: 500;
  margin: ${(props) => (props.$isEvent ? '0' : '0 7px')};
`
const TabsStyled = styled(Tabs)`
  border-top: 1px #e5e8f5 solid;
  border-bottom: 1px #e5e8f5 solid;
`

function TabPanel(props) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box pt={3}>{children}</Box>}
    </div>
  )
}

export default function SidebarLeft() {
  const [category, setCategory] = React.useState(0)    
  const { dispatch, getStageCenter, getSelectedNode } = useBoard()

  
  const handleChange = (event, newValue) => {
    if (!selectedNode && newValue === 1) {
      return
    }
    
    setCategory(newValue)
  }
  const getConfigs = (nodeSelected, asKeyValue = true) => {
    let configs = {}

    if (!nodeSelected) {
      return configs
    }

    if (!asKeyValue) {
      return eventTypes.find((el) => el.name === nodeSelected.name).config || []
    }

    return selectedNode.config.reduce((acc, curr) => {
      acc[curr.name] = curr.configValue ?? ''
      return acc
    })
    
  }
  const handleOnChangeSelect = (event, select) => {
    const {value} = event.target
    const id = select
    const newValues = configs;
    newValues[id] = value

    dispatch({
      type: SET_NODE_CONFIG,
      payload: {
        selectedNode,
        values: newValues,
      },
    })
    
    
  }
  const handleOnChange = (event) => {
    const {id, value, checked, type } = event.target
    const config = configs;
    config[id] = type === 'checkbox' ? checked : value
    dispatch({
      type: SET_NODE_CONFIG,
      payload: {
        selectedNode,
        values : config
      },
    })
    
  }
  

  const handleOnClick = (nodeSelected) => {
    const configs = getConfigs(nodeSelected, false)
    dispatch({
      type: ADD_NODE,
      payload: {
        ...nodeSelected,
        config: configs,
        position: getStageCenter(),
      },
    })
  }

  const renderConfig = (input) => {
    let { type, name, label, value, configValue, show, showOption } = input
    const configs = getConfigs(selectedNode)

    if (!showOption) {
      showOption = () => true
    }

    if (typeof label === 'function') {
      label = label(configs)
    }
    if (typeof value === 'function') {
      value = value(configs)
    }
    if (typeof configValue === 'function') {
      configValue = configValue(configs)
    }


    const key = `${name}-${label}-${type}-` + selectedNode.id
   
    const currentValue = configs[name] ?? configValue ?? ''



    if (typeof show === 'function' && !show(configs)) {
      return null
    }

    if (type === 'text') {
      return (<TextField style={{padding : '0.5rem'}} key={key} id={name} label={label} defaultValue={currentValue} fullWidth variant="outlined" onChange={handleOnChange} />)
    } else if (type === 'select') {
      return (
        <TextField style={{padding : '0.5rem'}} key={key} defaultValue={currentValue} id={name} label={label} fullWidth variant="outlined" select onChange={(e) => handleOnChangeSelect(e, name)}>
          {input.options.filter( (option) => showOption(option, configs)).map((option) => (
            <option key={key + '-op-' + option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
      )
    } else if (type === 'checkbox') {
      return (<TextField style={{padding : '0.5rem'}} key={key} id={name} label={label} variant="outlined" fullWidth type="checkbox" onChange={handleOnChange} defaultChecked={currentValue} />)
    } else if (type === 'number') {
      return (<TextField style={{padding : '0.5rem'}} key={key} id={name} label={label} variant="outlined" fullWidth type="number" onChange={handleOnChange} defaultValue={currentValue} />)
    } else if (type === 'radio') {
      return (<TextField style={{padding : '0.5rem'}} key={key} id={name} label={label}  fullWidth type="radio" onChange={handleOnChange} defaultChecked={currentValue} />)
    }

    return null

  }    

  const selectedNode = getSelectedNode();
  const configs = getConfigs(selectedNode)
  return (
    <>
      <TabsStyled
        value={category}
        onChange={handleChange}
        aria-label="Category tabs"
        variant="fullWidth"
        scrollButtons="off"
        indicatorColor="primary"
      >
        <Tab label="Ações" id="simple-tab-event" aria-controls="simple-tabpanel-event" />
        <Tab label="Configurações" id="simple-tab-config" aria-controls="simple-tabpanel-config"/> 
        
      </TabsStyled>
      <TabPanel value={category} index={0}>
        <Grid container justifyContent="space-evenly" spacing={1} style={{ rowGap: '1rem' }}>
          {eventTypes.map((event) => (
            <GridItemStyled key={JSON.stringify(event)} item $isEvent>
              <PaperStyled onClick={() => handleOnClick(event)} src={event.src} $isEvent />
              <Title $isEvent>{event.name}</Title>
            </GridItemStyled>
          ))}
        </Grid>
      </TabPanel>
      <TabPanel value={category} index={1}>
        { selectedNode &&
        <Grid container spacing={1}  style={{ rowGap: '1rem' }}>
            <Grid item xs={12}  >
              <TextField style={{padding : '0.5rem'}} key={selectedNode.name} id={selectedNode.name} defaultValue={selectedNode.label} label="Nome" fullWidth variant="outlined" onChange={(e) => {
                const value = e.target.value
                 dispatch({
                    type: SET_NODE_LABEL,
                    payload: {
                      selectedNode,
                      value,
                    },
                  })
              }} />
            { selectedNode.config.map((input) => renderConfig(input))}
            </Grid>

        </Grid>  
        }
      </TabPanel>
    
    </>
  )
}
