/* eslint-disable react/prop-types */
import { io } from 'socket.io-client';
import React, { useEffect } from 'react'
import uuid from 'react-uuid'
import {
  ADD_CONNECTION,
  ADD_NODE,
  COPY_NODE,
  REMOVE_CONNECTION,
  REMOVE_NODE,
  SET,
  SET_NODE_CONFIG,
  SET_NODE_LABEL,
  UPDATE_POSITIONS,
} from '../constants/actions'
import * as canvasServices from '~/services/canvasServices'
import { BoardContext, boardContextDefault } from './BoardInitialState'

const getElementIndex = (id, nodes) => nodes.findIndex((el) => el.id === id)

function boardReducer(state, action) {
  switch (action.type) {
    case SET_NODE_LABEL: {
      const { selectedNode, value } = action.payload
      const nodesUpdated = [...state.nodes]
      const index = getElementIndex(selectedNode.id, state.nodes)
      
      nodesUpdated[index].label = value
  
      return { ...state, nodes: nodesUpdated }
    }
    case SET_NODE_CONFIG: {
      const { selectedNode, values } = action.payload
      const nodesUpdated = [...state.nodes]
      const index = getElementIndex(selectedNode.id, state.nodes)
      nodesUpdated[index].config = nodesUpdated[index].config.map((config) => {
        const { name } = config
        return {
          ...config,
          configValue: values[name],
        }
      })
      
      return { ...state, nodes: nodesUpdated }
    }
    case ADD_NODE: {
      const nodesUpdated = [
        ...state.nodes,
        {
          ...action.payload,
          id: uuid(),
          match: [],
        },
      ]

      return { ...state, nodes: nodesUpdated }
    }
    case REMOVE_NODE: {
      const { id } = action.payload

      let nodesCopy = [...state.nodes]
      nodesCopy = nodesCopy.map((el) => {
        const index = el.match.indexOf(id)
        if (index !== -1) {
          el.match.splice(index, 1)
        }
        return el
      })
      const nodesUpdated = nodesCopy.filter((el) => el.id !== id)

      return { ...state, nodes: nodesUpdated }
    }
    case COPY_NODE: {
      const { id, position } = action.payload
      const index = getElementIndex(id, state.nodes)
      let nodesUpdated = [...state.nodes]

      nodesUpdated = [
        ...state.nodes,
        {
          ...nodesUpdated[index],
          id: uuid(),
          position: position,
          match: [],
        },
      ]

      return { ...state, nodes: nodesUpdated }
    }
    case ADD_CONNECTION: {
      const { id, to } = action.payload
      const nodesUpdated = [...state.nodes]
      const index = getElementIndex(id, state.nodes)

      nodesUpdated[index] = {
        ...nodesUpdated[index],
        match: [...nodesUpdated[index].match, to],
      }

      return { ...state, nodes: nodesUpdated }
    }
    case REMOVE_CONNECTION: {
      const { id, matchId } = action.payload
      const nodesUpdated = [...state.nodes]
      const elementIndex = nodesUpdated.findIndex((el) => el.id === id)

      const index = nodesUpdated[elementIndex].match.indexOf(matchId)
      if (index !== -1) {
        nodesUpdated[elementIndex].match.splice(index, 1)
      }
      return { ...state, nodes: nodesUpdated }
    }
    case UPDATE_POSITIONS: {
      return { ...state, nodes: action.payload }
    }
    case SET: {
      return { ...state, ...action.payload }
    }
    default: {
      throw new Error(`Unhandled action type: ${action.type}`)
    }
  }
}
const query = new URLSearchParams(window.location.search);
const URL =  query.get('url') || 'localhost'

const connection = io('http://'+ URL  +':4341');


function BoardProvider({ boardId, children }) {
  const { data, isFetching, runRequest } = canvasServices.get(boardId)
  const [board, dispatch] = React.useReducer(boardReducer, boardContextDefault)
  const [events, setEvents] = React.useState([])
  const stageRef = React.useRef(null)
  
  const setOnExecution = (event,cb) => {
    if (events.includes(event)) {
      return
    }
    setEvents([...events,event])
    connection.on(event,cb)
  }
  
  useEffect(() => {
    if (!board?.id) {
      dispatch({ type: SET, payload: data })
    }
  
   
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isFetching])

  const getStageCenter = () => {
    const stage = stageRef.current
    const scale = stage.scaleX()

    const pointer = {
      x: stage.width() / 2,
      y: stage.height() / 2,
    }

    const mousePointTo = {
      x: (pointer.x - stage.x()) / scale,
      y: (pointer.y - stage.y()) / scale,
    }

    return {
      x: mousePointTo.x,
      y: mousePointTo.y,
    }
  }

  const setSelectedNode = (id) =>  {
    dispatch({
      type: SET,
      payload: {
        selectedNodeId: id,
      },
    })
  }

  const getSelectedNode = () => {
    const node = board.nodes.find((el) => el.id === board.selectedNodeId)
    return node
  }

  const getPointWithScale = (pointer) => {
    const stage = stageRef.current
    const scale = stage.scaleX()

    const mousePointTo = {
      x: (pointer.x - stage.x()) / scale,
      y: (pointer.y - stage.y()) / scale,
    }

    return {
      x: mousePointTo.x,
      y: mousePointTo.y,
    }
  }

  const value = {
    board,
    isFetching,
    dispatch,
    runRequest,
    stageRef,
    getStageCenter,
    connection,
    setSelectedNode,
    getSelectedNode,
    getPointWithScale,
    setOnExecution,
  }

  return <BoardContext.Provider value={value}>{children}</BoardContext.Provider>
}

function useBoard() {
  const context = React.useContext(BoardContext)
  if (context === undefined) {
    throw new Error('useBoard must be used within a BoardProvider')
  }
  return context
}

export { BoardProvider, useBoard, getElementIndex }
