
import { eventTypeBuilder, configBuilder } from "./builder";
const selectorTypeConfig = configBuilder()
    .select('selectorType')
    .withLabel('Tipo de Seletor')
    .addOption('css', 'CSS')
    .addOption('xpath', 'XPath')
    .addOption('text', 'Texto')
    .withConfigValue('css')
const selectorConfig = configBuilder()
    .text('selector')
    .withLabel('Seletor')

const keyConfig = configBuilder()
    .select('key')
    .withLabel('Tecla')
    .addOption('enter', 'Enter')
    .addOption('tab', 'Tab')
    .addOption('space', 'Espaço')
    .addOption('backspace', 'Backspace')
    .addOption('delete', 'Delete')
    .addOption('shift', 'Shift')
    .addOption('control', 'Control')
    .addOption('alt', 'Alt')
    .addOption('left', 'Seta para Esquerda')
    .addOption('up', 'Seta para Cima')
    .addOption('right', 'Seta para Direita')
    .addOption('down', 'Seta para Baixo')
    .addOption('pageup', 'Page Up')
    .addOption('pagedown', 'Page Down')
    .addOption('end', 'End')
    .addOption('home', 'Home')
    .addOption('insert', 'Insert')
    .addOption('escape', 'Escape')
    .withConfigValue('enter')
    .withShow((config) => {
        return config.action === 'press'
    });


for (let i = 1; i <= 12; i++) {
    const key = 'F' + i.toString()
    keyConfig.addOption(key, key)
}




export const INPUT_EVENT = eventTypeBuilder()
    .whitType('event')
    .withId('input')
    .withName('Entrada')
    .withLabel('Entrada')
    .withSrc('https://cdn-icons-png.flaticon.com/512/3273/3273997.png')
    .addConfig(configBuilder()
        .select('action')
        .withLabel('Ação')
        .addOption('type', 'Digitar')
        .addOption('clear', 'Limpar')
        .addOption('select', 'Selecionar')
        .addOption('setFile', 'Selecionar Arquivo')
        .addOption('press', 'Pressionar Tecla')
        .withConfigValue('type')
        .build()
    )
    .addConfig(configBuilder()
        .text('text')
        .withLabel(({action}) => {
            if (action === 'setFile') {
                return 'Caminho do Arquivo'
            }
            return action === 'select' 
                ?  'Valor a ser Selecionado'
                :  'Texto'
        })
        .withShow(({action, key}) => {
            return action === 'select' || action === 'type'  || action === 'setFile'  || (action === 'press' && key === 'other')
        })
        .build()
    )
    .addConfig(configBuilder()
        .checkbox('clear')
        .withLabel('Limpar Campo')
        .withShow((config) => {
            return config.action === 'clear'
        })
        .build()
    )
    .addConfig(keyConfig.build())
    .addConfig(selectorTypeConfig
        .withShow(({action}) => {
            return action !== 'press'
        })
        .build()
    )
    .addConfig(selectorConfig
        .withShow(({action}) => {
            return action !== 'press'
        })
        .build()
    ).addConfig(configBuilder()
        .number('timeout')
        .withLabel('Tempo Pressionado')
        .withConfigValue(100)
        .withShow(({action}) => {
            return action === 'press'
        })
        .build()
    )
    .build()

        