import { eventTypes } from '~/pages/canvas-draw/utils'


export const getAll = () => {}

export const get = (id) => {
    const json = require('./nodes.json');

    if (json) {
        const result =  json

        result.nodes = result.nodes.map((el) => {
            const eventType = eventTypes.find((e) => e.name === el.name);
            if (!eventType) {
                return el
            }

            el.config = eventType.config.map((eventTypeConfig) => {
                const config = el.config.find((e) => e.name === eventTypeConfig.name) || {}
                return {...eventTypeConfig, ...config}
            })

            return el
        })

        console.log('result', result)
        return {data :  result, isFetching: false}
    }

    return {"id":40,"name":"test","nodes":[]}
}

export const save = (name) => {}

export const update = (id, data, connection) => {

    localStorage.setItem('test.json', JSON.stringify(data))
    const name = data.name ? data.name : 'test'
    const nodes = data.nodes.map((el) => {
        const nodeConfig = el.config
            .reduce((acc, curr) => {
                acc[curr.name] = curr.configValue
                return acc
            }, {});

        return {
            name: el.name,
            id: el.id,
            config: Object.keys(nodeConfig).reduce((acc, key) => {
                let store = true
                const curr = el.config.find((e) => e.name === key)

                if ( typeof curr.show === 'function') {
                    console.log('curr', curr, el)
                    store = curr.show(nodeConfig)
                }

                if (store) {
                    acc[curr.name] = nodeConfig[key]
                }
                
        
                return acc
            }, {})
        }
    })

    connection.emit('execute',{name, nodes })

    

    
}

